<template>
  <div class="welcome">
  	<div class="cloud"><img src="../assets/cloud.jpg" alt="" /></div>
    <div class="cloud"><img src="../assets/cloud.jpg" alt="" /></div>
    <div class="cloud"><img src="../assets/cloud.jpg" alt="" /></div>
    <div class="parachute"><img src="../assets/parachute.jpg"/></div>
    <div class="title">{{ msg }}</div>
    <div class="start"><router-link to="/pages/article">start</router-link></div>
  </div>
</template>

<script>
export default {
  name: 'welcome',
  data () {
    return {
      msg: '寻找人间的真善美'
    }
  }
}
</script>

