<style scoped>
		@keyframes -webkit-parachute{
			100%{-webkit-transform: translateY(0);opacity: 1;}
		}
		@keyframes -webkit-cloud{
			0%{-webkit-transform: translateX(0);}
			10%{-webkit-transform: translateX(-0.1rem);}
			20%{-webkit-transform: translateX(-0.2rem);}
			30%{-webkit-transform: translateX(-0.3rem);}
			40%{-webkit-transform: translateX(-0.4rem);}
			50%{-webkit-transform: translateX(-0.5rem);}
			60%{-webkit-transform: translateX(-0.4rem);}
			70%{-webkit-transform: translateX(-0.3rem);}
			80%{-webkit-transform: translateX(-0.2rem);}
			90%{-webkit-transform: translateX(-0.1rem);}
			100%{-webkit-transform: translateX(0);}
		}
		@keyframes -webkit-title{
			100%{-webkit-transform: translateY(0);opacity: 1;}
		}
		@keyframes -webkit-start{
			100%{-webkit-transform: rotateX(0);}
		}
		
		@keyframes -moz-parachute{
			100%{-webkit-transform: translateY(0);opacity: 1;}
		}
		@keyframes -moz-cloud{
			0%{-moz-transform: translateX(0);}
			10%{-moz-transform: translateX(-0.1rem);}
			20%{-moz-transform: translateX(-0.2rem);}
			30%{-moz-transform: translateX(-0.3rem);}
			40%{-moz-transform: translateX(-0.4rem);}
			50%{-moz-transform: translateX(-0.5rem);}
			60%{-moz-transform: translateX(-0.4rem);}
			70%{-moz-transform: translateX(-0.3rem);}
			80%{-moz-transform: translateX(-0.2rem);}
			90%{-moz-transform: translateX(-0.1rem);}
			100%{-moz-transform: translateX(0);}
		}
		@keyframes -moz-title{
			100%{-webkit-transform: translateY(0);opacity: 1;}
		}
		@keyframes -moz-start{
			100%{-webkit-transform: rotateX(0);}
		}
		
		@keyframes -o-parachute{
			100%{-o-transform: translateY(0);opacity: 1;}
		}
		@keyframes -o-cloud{
			0%{-o-transform: translateX(0);}
			10%{-o-transform: translateX(-0.1rem);}
			20%{-o-transform: translateX(-0.2rem);}
			30%{-o-transform: translateX(-0.3rem);}
			40%{-o-transform: translateX(-0.4rem);}
			50%{-o-transform: translateX(-0.5rem);}
			60%{-o-transform: translateX(-0.4rem);}
			70%{-o-transform: translateX(-0.3rem);}
			80%{-o-transform: translateX(-0.2rem);}
			90%{-o-transform: translateX(-0.1rem);}
			100%{-o-transform: translateX(0);}
		}
		@keyframes -o-title{
			100%{-webkit-transform: translateY(0);opacity: 1;}
		}
		@keyframes -o-start{
			100%{-webkit-transform: rotateX(0);}
		}
		
		
		@keyframes parachute{
			100%{transform: translateY(0);opacity: 1;}
		}
		@keyframes cloud{
			0%{transform: translateX(0);}
			10%{transform: translateX(-0.1rem);}
			20%{transform: translateX(-0.2rem);}
			30%{transform: translateX(-0.3rem);}
			40%{transform: translateX(-0.4rem);}
			50%{transform: translateX(-0.5rem);}
			60%{transform: translateX(-0.4rem);}
			70%{transform: translateX(-0.3rem);}
			80%{transform: translateX(-0.2rem);}
			90%{transform: translateX(-0.1rem);}
			100%{transform: translateX(0);}
		}
		@keyframes title{
			100%{transform: translateY(0);opacity: 1;}
		}
		@keyframes start{
			100%{transform: rotateX(0);}
		}
		
		
		.welcome{width: 15rem;height: 100%;background: url(../assets/welcome.jpg) no-repeat;
		background-size: 100% 100%;background-position: center;
		position: fixed;}
		.cloud{position: absolute;width: 3.4rem;height: 2.1rem;
		}
		.cloud img{width: 3.4rem;height: 2.1rem;}
		.cloud:nth-of-type(1){left: 0.6rem;top: 1.5rem;
		-webkit-animation: cloud 4s 2s infinite alternate linear;
		-moz-animation: cloud 4s 2s infinite alternate linear;
		-o-animation: cloud 4s 2s infinite alternate linear;
		animation: cloud 4s 2s infinite alternate linear;}
		.cloud:nth-of-type(2){left: 1.4rem;top: 8.8rem;
		-webkit-animation: cloud 4s 2.4s infinite alternate linear;
		-moz-animation: cloud 4s 2.4s infinite alternate linear;
		-o-animation: cloud 4s 2.4s infinite alternate linear;
		animation: cloud 4s 2.4s infinite alternate linear;}
		.cloud:nth-of-type(3){left: 11rem;top: 9.6rem;
		-webkit-animation: cloud 4s 2.6s infinite alternate linear;
		-moz-animation: cloud 4s 2.6s infinite alternate linear;
		-0-animation: cloud 4s 2.6s infinite alternate linear;
		animation: cloud 4s 2.6s infinite alternate linear;}
		.parachute{position: absolute;width: 5.56rem;height: 6.76rem;
		left: 4.7rem;top: 4.1rem;}
		.parachute img{width: 5.56rem;height: 6.76rem;opacity: 0;
		-webkit-transform: translateY(2rem);
		-moz-transform: translateY(2rem);
		-o-transform: translateY(2rem);
		transform: translateY(2rem);
		-webkit-animation: parachute 1s;-webkit-animation-fill-mode: forwards;
		-moz-animation: parachute 1s;-webkit-animation-fill-mode: forwards;
		-o-animation: parachute 1s;-webkit-animation-fill-mode: forwards;
		animation: parachute 1s;animation-fill-mode: forwards;
		}
		.title{width: 12rem;height: 3rem;color: #646564;text-align: center;position: absolute;
		top: 11.6rem;left: 1.5rem;line-height: 3rem;
		-webkit-transform: translateY(2rem);
		-moz-transform: translateY(2rem);
		-o-transform: translateY(2rem);
		transform: translateY(2rem);
		opacity: 0;
		-webkit-animation: title 1.5s;-webkit-animation-fill-mode: forwards;
		-moz-animation: title 1.5s;-webkit-animation-fill-mode: forwards;
		-0-animation: title 1.5s;-webkit-animation-fill-mode: forwards;
		animation: title 1.5s;animation-fill-mode: forwards;
		}
		.start{width: 8rem;height: 2.2rem;background: rgba(0,0,0,.5);
		position: absolute;left: 3.5rem;top: 14.6rem;border: 0.1rem solid #fff;
		border-radius: 1rem;text-align: center;line-height: 2.2rem;
		-webkit-transform: rotateX(90deg);
		-moz-transform: rotateX(90deg);
		-0-transform: rotateX(90deg);
		transform: rotateX(90deg);
		-webkit-animation: start 1s;-webkit-animation-fill-mode: forwards;
		-webkit-animation-delay:1s;
		-moz-animation: start 1s;-webkit-animation-fill-mode: forwards;
		-moz-animation-delay:1s;
		-o-animation: start 1s;-webkit-animation-fill-mode: forwards;
		-o-animation-delay:1s;
		animation: start 1s;animation-fill-mode: forwards;
		animation-delay:1s;}
		.start a{display: block;width: 8rem;height: 2.2rem;border-radius: 1rem;
		text-decoration: none;color: #fff;}
</style>



