<template>
  <div class="article">
    <transition name="fade" mode="out-in">
    	<router-view></router-view>
    </transition>
    <ul class="foots">
    	<li class="items" @click="change(0)">
    		<router-link to="/pages/article">
    			<img v-show="seen[0]" src="../assets/document.png" alt="" />
    			<img v-show="!seen[0]" src="../assets/document_fill.png" alt="" />
    			<span>文章</span>
    		</router-link>
    	</li>
    	<li class="items" @click="change(1)">
    		<router-link to="/pages/like">
    			<img v-show="seen[1]" src="../assets/like.png" alt="" />
    			<img v-show="!seen[1]" src="../assets/like_fill.png" alt="" />
    			<span>爱好</span>
    		</router-link>
    	</li>
    	<li class="items" @click="change(2)">
    		<router-link to="/pages/message">
    			<img v-show="seen[2]" src="../assets/interactive.png" alt="" />
    			<img v-show="!seen[2]" src="../assets/interactive_fill.png" alt="" />
    			<span>留言</span>
    		</router-link>
    	</li>
    	<li class="items" @click="change(3)">
    		<router-link to="/pages/about">
    			<img v-show="seen[3]" src="../assets/people.png" alt="" />
    			<img v-show="!seen[3]" src="../assets/people_fill.png" alt="" />
    			<span>关于</span>
    		</router-link>
    	</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      seen:[false,true,true,true]
    }
  },
  methods:{
  	change:function(index){
  		this.seen=[true,true,true,true];
  		this.seen.splice(index,1,"")
  	}
  }
}
</script>
<style scoped>
		.foots{margin: 0;padding: 0;width: 15rem;height: 2.4rem;position: fixed;bottom: 0;
		background: #262320;box-sizing: border-box;
		border-top: 0.02rem solid #ddd;}
		.foots .items{list-style: none;width: 25%;height: 2.4rem;float: left;
		text-align: center;line-height: 2.4rem;font-size: 0.4rem;position: relative;}
		.items a{text-decoration: none;display: block;width: 100%;height: 2.4rem;color: #63727b;}
		.items a img{width: 1.4rem;height: 1.4rem;margin-top: 0.1rem;}
		.items a span{display: block;position: absolute;left: 0.8rem;bottom: 0.5rem;
		width: 2.15rem;height: 0.5rem;
					line-height: 0.5rem;}
		.router-link-active span{color: #1296DB;}
		.fade-enter,.fade-leave-to{
			opacity: 0;
		}
		.fade-enter-active,.fade-leave-active{
			transition: opacity .3s
		}
</style>



