<template>
	<div class="msg">
		<ul>
  		<li v-for="(item,index) in msg">
  			<span>{{index+1}}楼:</span><span>{{item}}</span>
  		</li>
  	</ul>
	  <div class="message">
	  	<div class="text">
	  		<textarea v-model="words"></textarea>
	  	</div>
	    <a class="send" href="javascript:;" @click="add">留下你的足迹</a>
	  </div>
	</div>
</template>

<script>
export default {
  name: 'message',
  data:function(){
  	return {
  		words: "",
  		msg:[]
  	}
  },
  methods:{
  	add:function(){
  		this.msg.push(this.words);
  		this.words=""
  	}
  }
}
</script>

<style scoped>
		.msg{width: 15rem;height: 100%;position: relative;}
		.message{width: 15rem;height: 100%;position: fixed;background: url(../assets/messageBg.jpg) no-repeat;
		background-size: 100% 100%;}
		.send{display: block;width: 8.2rem;height: 1.9rem;position: absolute;top: 16rem;left: 3.4rem;
		text-decoration: none;text-align: center;line-height: 1.9rem;color: #fff;background: #fe4c64;
		font-size: 0.9rem;}
		.text{width: 12rem;height: 2.2rem;position: absolute;top: 13.5rem;left: 1.5rem;
		border-radius: 2rem;overflow: hidden;box-sizing: border-box;
		opacity: .6;background: rgba(0,0,0,0.5);}
		.text textarea{border: none;outline: none;resize: none;width: 12rem;height: 2.2rem;
		border-radius: 2rem;line-height: 1.1rem;font-size: 1rem;opacity: .6;
		padding-left: 1rem}
		ul{margin: 0;padding: 0;position: absolute;top: 0;left: 0;z-index: 3;}
		ul li{width: 15rem;list-style: none;text-align: left;color: #fff;padding: 0.3rem;
		
		font-size: 0.8rem;line-height: 1rem;box-sizing: border-box;word-break: break-all;}
</style>


